// Yardmc fonksiyonlar
export const homeHelpers = {
  // Otomatik slider iin yardmc fonksiyon
  autoSlide: (currentSlide: number, totalSlides: number, setCurrentSlide: (slide: number) => void) => {
    const nextSlide = (currentSlide + 1) % totalSlides;
    setCurrentSlide(nextSlide);
  }
}; 