/* eslint-disable @typescript-eslint/no-explicit-any */
import { createClient } from '@supabase/supabase-js';

// NOT: Bu yaklam sadece gelitirme aamasnda kullanlmaldr!
// Gerek projelerde ortam deikenlerini kullann.
const supabaseUrl = import.meta.env.VITE_SUPABASE_URL;
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY;

// Deerlerin bo olmadndan emin olalm
if (!supabaseUrl || !supabaseAnonKey) {
  console.error('Supabase URL veya Anon Key bulunamad. Ltfen .env dosyasn kontrol edin.');
}

// WebSocket'i monkey patch ederek engelleme
if (typeof window !== 'undefined') {
  const originalWebSocket = window.WebSocket;
  // @ts-ignore
  window.WebSocket = function(url, ...args) {
    if (url.includes('supabase.co/realtime')) {
      console.log('Blocked WebSocket connection to Supabase Realtime');
      // Sahte bir WebSocket nesnesi dndr
      return {
        addEventListener: () => {},
        removeEventListener: () => {},
        send: () => {},
        close: () => {},
        onopen: null,
        onclose: null,
        onmessage: null,
        onerror: null,
      };
    }
    // Dier WebSocket balantlarna izin ver
    return new originalWebSocket(url, ...args);
  };
  // Prototip zincirini koru
  window.WebSocket.prototype = originalWebSocket.prototype;
}

// Realtime zelliini tamamen devre d brakarak istemciyi oluturun
export const supabase = createClient(
  supabaseUrl || '',
  supabaseAnonKey || '',
  {
    auth: {
      persistSession: true,
      autoRefreshToken: true
    },
    global: {
      headers: { 'x-application-name': 'algoritma-arastirma' }
    },
    realtime: {
    },
    db: {
      schema: 'public'
    }
  }
);

// WebSocket balantsn manuel olarak kapatma giriimi
try {
  // @ts-ignore - Supabase tiplerinde bu zellik aka belirtilmemi olabilir
  if (supabase.realtime && typeof supabase.realtime.disconnect === 'function') {
    supabase.realtime.disconnect();
  }
} catch (error) {
  console.log('Realtime disconnect attempt ignored');
}

// Geriye dnk uyumluluk iin default export
export default supabase;