import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { supabase } from '../lib/supabase';
import { Session, User, WeakPassword } from '@supabase/supabase-js';

// AuthContext iin tip tanm
export interface AuthContextType {
  user: User | null;
  session: Session | null;
  loading: boolean;
  login: (email: string, password: string) => Promise<{ user: User; session: Session; weakPassword?: WeakPassword } | void>;
  logout: () => Promise<void>;
  isAuthenticated: boolean;
}

// Varsaylan deerler
const defaultContext: AuthContextType = {
  user: null,
  session: null,
  loading: true,
  login: async () => {},
  logout: async () => {},
  isAuthenticated: false
};

const AuthContext = createContext<AuthContextType>(defaultContext);

// eslint-disable-next-line react-refresh/only-export-components
export const useAuth = () => useContext(AuthContext);

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [session, setSession] = useState<Session | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Mevcut oturumu kontrol et
    const getSession = async () => {
      setLoading(true);
      
      const { data: { session }, error } = await supabase.auth.getSession();
      
      if (error) {
        console.error('Oturum alnamad:', error);
      }
      
      setSession(session);
      setUser(session?.user ?? null);
      setLoading(false);
    };

    getSession();

    // Oturum deiikliklerini dinle
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        setSession(session);
        setUser(session?.user ?? null);
        setLoading(false);
      }
    );

    return () => {
      subscription.unsubscribe();
    };
  }, []);

  // Giri fonksiyonu
  const login = async (email: string, password: string) => {
    try {
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password,
      });
      
      if (error) throw error;
      
      // Oturum sresini 8 saat ile snrla
      setTimeout(() => {
        logout();
      }, 8 * 60 * 60 * 1000);
      
      return data;
    } catch (error) {
      console.error('Giri hatas:', error);
      throw error;
    }
  };

  // k fonksiyonu
  const logout = async () => {
    try {
      await supabase.auth.signOut();
      // Yerel depolama temizlii
      localStorage.removeItem('supabase.auth.token');
      // Durum gncelleme
      setUser(null);
      setSession(null);
    } catch (error) {
      console.error('k hatas:', error);
    }
  };

  const value: AuthContextType = {
    user,
    session,
    loading,
    login,
    logout,
    isAuthenticated: !!user
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}; 